.group-card-container {
  height: 95vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.group-card-wrapper {
  display: flex;
  flex-direction: row;
  text-align: center;
  padding: 3%;
  height: 52%;
  width: 60%;
  border-radius: 19px;
  background: #9ccac5;
  box-shadow: inset -11px 11px 22px #bababa,
  inset 11px -11px 22px #ffffff;}

.group-card-details {
  flex: 1;
  width: 80%;
}
.group-card-details p,
h2 {
  color: #253649;
}

.attendees {
  /* flex: 2 right; */
  width: 35%;
  text-align: start !important;
  color: #253649;
  height: 80%;
}

.users {
  color: #253649;
}

.gw-card-image{
  height: 70%;
  width: auto;
}


.gc-button{
  /* background-color: #9ccac5; */
  background-color: white;
  align-items: center;
  margin-top: 5%;
  margin-left: 20%;
  width: 40%;
  padding: 5px;
  border-radius: 20px;
  box-shadow: -7px 7px 30px #ffffff, 7px -7px 100px #9ccac5;
  cursor: pointer;

}
.button-container{
  /* justify-content: end; */
  text-align: end;
  margin-top: 100%;
}

.gc-button:hover{
  background-color: #9ccac5;
}
.gc-button-info{
  color: #9ccac5;
  text-decoration: none;
  font-size: 15px;
  font-variant: small-caps;
  font-weight: bold;
}
.gc-button-info:hover{
  color: white;

}

.attendees a {
  color: #253649;
  font-size: 15px;
}
